import React from "react";
import styles from "./Socials.module.scss";
import Facebook from "./images/assets/facebook.inline.svg";
import Instagram from "./images/assets/instagram.inline.svg";
import Twitter from "./images/assets/twitter.inline.svg";
import Linkedin from "./images/assets/linkedin.inline.svg";
const Socials = () => {
  return (
    <div className={styles["container"]}>
      <ul className={styles["social-container"]}>
        <li>
          <Facebook width="25px" fill="black"></Facebook>
        </li>
        <li>
          <Instagram width="25px" fill="black"></Instagram>
        </li>
        <li>
          <Twitter width="25px" fill="black"></Twitter>
        </li>
        <li>
          <Linkedin width="25px" fill="black"></Linkedin>
        </li>
      </ul>
    </div>
  );
};

export default Socials;
