import React, { useEffect } from "react";
import Home from "./Home/Home";
import LowerHomeBar from "./LowerHomeBar/LowerHomeBar";
import Discover from "./Discover/Discover";import Socials from "./Socials/Socials";
import "./../main.scss";
const pages = [];
for (let i = 0; i < 3; i++) {
  pages.push("");
}
let observer = null;

const IndexWrapper = () => {
  useEffect(() => {
    let callback = (entries, observer) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.style.opacity = 1;
          entry.target.style.transform = "translateY(0px)";
        }
      });
    };
    let options = {
      rootMargin: "0px",
      threshold: 0.1,
    };

    observer = new IntersectionObserver(callback, options);
    const obsEles = document.body.querySelectorAll('div[name="page-content"]');
    obsEles.forEach((ele) => {
      observer.observe(ele);
    });
  }, []);
  return (
    <div className="root">
      <Home />
      <LowerHomeBar></LowerHomeBar>
      {pages.map((ele, i) => {
        return (
          <>
            <Discover index={i}></Discover>
          </>
        );
      })}
      <Socials></Socials>
    </div>
  );
};

export default IndexWrapper;
